<template>
	<view>
		<v-search></v-search>
		<sharing-list></sharing-list>
		<u-button @click="show=true" class="login-btn">退出登录</u-button>
		<u-modal v-model="show" show-cancel-button @confirm="logOutHandle" @cancel="show=false" :content="content"></u-modal>
	</view>
	
</template>

<script>
	import {
		mapState,
		mapMutations
	} from 'vuex';
	import vSearch from "@/components/v-search";
	import sharingList from "../article/sharing-list"
	export default {
		components: {
			vSearch,sharingList
		},
		data() {
			return {
				isxyzqemp:'',
				bannerList:[],
				current: 0,
				show:false,
				content:'是否退出登录?',
			};
		},
		computed: mapState([ 'hasLogin','uerInfo','uerInfo2']),
		onLoad(option) {

		},
		mounted() {
			
		},

		methods: {
			...mapMutations(['logout']),
			logOutHandle() {
				if (this.hasLogin) {
					this.logout()
				} 
				uni.navigateTo({
					url: "/pages/login/index"
				})
			},
		}
	};
</script>

<style lang="scss" scoped>
.wrap {
		padding: 20rpx;
	}
	
</style>
